<template>
  <div id="game-map">
    <h2>游戏地图</h2>
    <div id="map"></div>
  </div>
</template>


<script>
import L from 'leaflet';
import axios from 'axios';


export default {
  name: 'GameMap',
  data() {
    return {
      map: null
    };
  },
  mounted() {
    try {
      this.initMap();
      this.getUserLocation();
    } catch (error) {
      console.error('地图初始化或获取用户位置时出错:', error);
    }
  },
  methods: {
    initMap() {
      try {
        this.map = L.map('map').setView([51.505, -0.09], 13);


        L.tileLayer('https://tile-b.openstreetmap.fr/hot/{z}/{x}/{y}.png', {
          maxZoom: 19
        }).addTo(this.map);


        console.log('地图已成功初始化');
      } catch (error) {
        console.error('地图初始化失败:', error);
      }
    },
    getUserLocation() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          (position) => {
            const userLocation = {
              latitude: position.coords.latitude,
              longitude: position.coords.longitude
            };
            this.sendUserLocationToBackend(userLocation);
            this.showUserLocationOnMap(userLocation);
          },
          (error) => {
            console.error('获取位置失败:', error);
          }
        );
      } else {
        console.error('浏览器不支持地理定位');
      }
    },
    showUserLocationOnMap(userLocation) {
      if (this.map) {
        try {
          const marker = L.marker([userLocation.latitude, userLocation.longitude]).addTo(this.map);
          marker.bindPopup('你的位置').openPopup();
          this.map.setView([userLocation.latitude, userLocation.longitude], 15);
          console.log('用户位置已成功显示在地图上');
        } catch (error) {
          console.error('显示用户位置失败:', error);
        }
      } else {
        console.error('地图未初始化，无法显示用户位置');
      }
    },
    sendUserLocationToBackend(userLocation) {
      axios.post('http://127.0.0.1:8000/api/user_location/', userLocation)
    .then(() => {
            console.log('用户位置已发送到后端');
          })
    .catch(error => {
            console.error('发送用户位置到后端失败:', error);
          });
    }
  }
};
</script>


<style scoped>
#game-map {
  height: 500px;
  width: 100%;
  border: 1px solid #ccc;
  position: relative;
}


#map {
  height: 450px;
  width: 100%;
}
</style>