<template>
    <div v-if="showHint">
      <div class="hint-modal">
        <h3>提示</h3>
        <p>{{ hintText }}</p>
        <button @click="closeHint">关闭提示</button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        showHint: false,
        hintText: ''
      };
    },
    methods: {
      showHintModal(text) {
        this.hintText = text;
        this.showHint = true;
      },
      closeHint() {
        this.showHint = false;
      }
    }
  };
  </script>
  
  <style scoped>
  .hint-modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 20px;
    border: 1px solid #ccc;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  }
  </style>