<template>
  <div>
    <input v-model="studentId" placeholder="请输入学号">
    <button @click="registerUser">注册</button>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      studentId: ''
    };
  },
  methods: {
    registerUser() {
      // 在这里发送注册请求到后端
      axios.post('http://127.0.0.1:8000/api/register/', { studentId: this.studentId })
   .then(() => {
        window.alert('注册成功');
        // 可以进行一些后续操作，如提示用户注册成功等
      })
   .catch(error => {
        console.error('注册失败:', error);
      });
    }
  }
};
</script>