<template>
    <div>
      <h2>任务列表</h2>
      <ul>
        <li v-for="task in tasks" :key="task.id">
          <span :class="{ 'completed': task.completed }">{{ task.name }}</span>
          <p v-if="task.description">{{ task.description }}</p>
          <button v-if="!task.completed" @click="startTask(task)">开始任务</button>
          <button v-if="task.inProgress" @click="completeTask(task)">完成任务</button>
        </li>
      </ul>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        tasks: [
          { id: 1, name: '找到神秘地点', description: '在地图上寻找标记的神秘地点', completed: false, inProgress: false },
          { id: 2, name: '解谜挑战', description: '解开谜题获取线索', completed: false, inProgress: false }
        ]
      };
    },
    methods: {
      startTask(task) {
        task.inProgress = true;
        // 这里可以添加开始任务的逻辑，例如发送请求到后端
      },
      completeTask(task) {
        task.completed = true;
        task.inProgress = false;
        // 这里可以添加完成任务的逻辑，例如更新任务状态并发送请求到后端
      }
    }
  };
  </script>
  
  <style scoped>
  .completed {
    text-decoration: line-through;
  }
  </style>